.outer {
  width: 66.6667%;
  text-align: center;
  border-radius: 12px;
  margin: 0;
  padding: 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.outer > .left {
  justify-content: left;
  margin-top: 40px;
}

.outer > .left ~ .left {
  margin-top: 0;
}

.circle {
  width: 130px;
  height: 130px;
  cursor: pointer;
  background-color: #efefef;
  border-radius: 50%;
  margin: 0 auto 92px;
}

.circle:hover {
  opacity: .5;
  background: #000;
}

.circle__text {
  color: #fff;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.circle__text:hover {
  opacity: 1;
}

.circle__text > input[type="file"] {
  z-index: -1;
  opacity: 0;
  width: 0;
  position: absolute;
}

.circle__text > label {
  cursor: pointer;
  z-index: 1;
  color: #fff;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.infoBlock {
  border-bottom: 1px solid #eaeaea;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  display: flex;
}

.infoBlock__label {
  text-align: left;
  font-size: 16px;
  font-weight: 500;
  line-height: 16px;
}

.profileInput {
  color: #999;
  text-align: right;
  background-color: #fff;
  border: none;
  font-size: 16px;
  line-height: 16px;
}

.exit {
  color: red;
}

.saveBlock {
  margin-top: 48px;
}

.return {
  width: 64px;
  height: 100%;
  cursor: pointer;
  background-color: #fbfbfb;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.return:hover {
  background-color: #dadada;
}

